.container{
  width: 300px;
  height: 500px;
  border: 1.5px solid;
  padding: 10px;
  background: linear-gradient(to right,rgb(210, 184, 101),rgba(167, 134, 68, 0.798));
  margin: auto;
  position: absolute;
  top: -10px; 
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.input{
  position: absolute;
  width: 50%;
  top: 5px;
}

.button{
  position: absolute;
  width: 28%;
  top: 28px;
  
}

.list{
  position: absolute;
  width: 28%;
  top: 40px;
  left: 0%;
}

.buttons {
  background-color: #d30d0d; /* Change this color to your desired button color */
  border: 1px solid black;
  padding: 5px 5px;
  cursor: pointer;
  border-radius: 10px; /* Adjust the border-radius to control the roundness */
  margin-right: 5px; /* Add margin if needed */
  position: relative;
  left: 3px;
  bottom: 5px;
}

.items{
  position: relative;
  left: 19px;
  bottom: 22px;
}